.checkout-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(0, 0, 0, 0.425);
  border-radius: 8px;
  display: flex;
  min-height: 750px;
  margin: 45px auto;
  width: 80%;
  max-width: 2000px;
  padding-top: 30px;
}

.checkout-container-2 {
  width: 95%;
}

.checkout-top-content h3{
  margin-left: 25px;
}

.checkout-top-content {
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(0, 0, 0, 0.325);
  border-radius: 3px;
  width: 100%;
  max-height: 330px;
  padding-bottom: 30px;
  padding-top: 15px;
  margin-top: 30px;
  overflow-y: scroll;
}

.checkout-product-container {
  display: flex;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 
  Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  margin: 0 0 10px 15px;
  width: 90%;
  height: 70px;
}

.checkout-product-container span {
  margin-left: 15px;
  font-size: 14px;
}

.checkout-product-container h4 {
  font-weight: 500;
}

.checkout-product-container img {
  height: 50px;
}

.checkout-hr {
  width: 96%;
  margin-top: 15px;
  margin-bottom: 15px;
  border: 0.3px solid rgba(97, 96, 96, 0.125);
}

.price-div {
  width: 15%;
  display: flex;
  flex-direction: row;
  margin-left: 10px;
}

.price-div h3, p{margin: 0; padding: 0;}

.price-div p {font-weight: 500;}

.price-div span{font-size: 14px;font-weight: 500;}

.form-container {
  margin: 30px auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(0, 0, 0, 0.325);
  border-radius: 3px;
}

.inputs-div {
  width: 100%;
  margin: 0;
  padding: 0;
}

.inputs-div input {
  padding-left: 8px !important;
  font-size: 15px !important;
}

.form-top-content input, .form-bottom-content input {
  height: 20px;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.425);
  padding: 3px;
  margin: 0 7px;
  height: 30px;
  width: 30%;
}

.form-container h3 {
  margin-left: 25px;
}

.form-top-content, .form-bottom-content {
  display: flex;
  justify-content: space-between;
}

.form-middle-content {
  display: flex;
  justify-content: space-between;
}

.form-top-content, .form-middle-content, .form-bottom-content {
  margin-bottom: 20px;
}

.form-middle-content input {
  height: 20px;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.425);
  padding: 3px;
  margin: 0 7px;
  height: 30px;
  width: 50%;
}

.payment-container {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-flow: wrap column;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(0, 0, 0, 0.325);
  border-radius: 4px;
  display: flex;
  height: 230px;
  margin: 0px auto 30px auto;
  width: 100%;
}

.payment-container h3 {
  margin-left: 15px;
}

.payment-methods {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 95%;
  height: 165px;
  margin-left: 15px;
}

.boleto-div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 125px;
  height: 100px;
  padding-right: 30px;
}

.card-div {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 100px;
}

.card-div img {
  width: 30%;
}

.creditcard {
  margin-left: 30px;
}

input[type=radio] {
  border: 0px;
  width: 20px;
  height: 20px;
}

.checkout-buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 63%;
  margin-bottom: 20px;
}

.checkout-buttons i {
  font-size: 50px;
}

.checkout-buttons i:hover {
  opacity: 80%;
}

.checkout-button {
  background: #3483fa;
  border-radius: 5px;
  border: none;
  color: white;
  font: 100 25px Arial , Helvetica , sans-serif;
  line-height: 23.94px;
  text-align: center;
  text-decoration: none;
  text-shadow: rgba(0, 0, 0, 0.5) 0 1px 2px;
  width: 350px;
  height: 65px;
  transition: .4s;
  outline: none;
}

.checkout-button:disabled {
  background: #144fa7;
  color: rgb(95, 94, 94);
}

.checkout-button:hover:enabled {
  opacity: 80%;
  cursor: pointer;
}

@media (max-width:1650px) {
  .checkout-buttons {
    width: 65%;
  }
}

@media (max-width:1450px) {
  .checkout-buttons {
    width: 68%;
  }
}

@media (max-width:1250px) {
  .checkout-buttons {
    width: 72%;
  }
}

@media (max-width:950px) {
  .checkout-buttons button{
    width: 60%;
  }
}

@media (max-width:720px) {
  .card-div {
    height: 150px;
  }

  .payment-container input {
    width: 15px;
  }
}

@media (max-width:710px) {
  .checkout-buttons button{
    width: 60%;
    font-size: 18px;
    height: 50px;
  }

  .payment-methods {
    justify-content: center;
  }

  .inputs-div input {
    height: 23px;
  }

  .payment-container input {
    width: 13px;
  }

  .checkout-container h3 {
    font-size: 14px;
  }

  .boleto-div {
    width: 60%;
  }

  .boleto-div img {
    width: 80%;
  }

  .card-div {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    height: 150px;
  }

  .card-div img{
    width: 30px;
  }

  .price-div {
    margin-left: 35px;
    font-size: 12px;
  }

}